<template>
  <div>
    <el-dialog
      title="添加合同"
      :visible.sync="addVisible"
      width="1200px"
    >
      <el-form
        :model="contractForm"
        :inline="true"
        ref="contractForm"
        :rules="rules"
      >
        <el-form-item
          label="客户名称"
          :label-width="formLabelWidth"
          prop="customerName"
        >
          <el-input v-model="contractForm.customerName"></el-input>
        </el-form-item>
        <el-form-item
          label="客户号"
          :label-width="formLabelWidth"
          prop="customerNo"
        >
          <el-input v-model="contractForm.customerNo"></el-input>
        </el-form-item>
        <el-form-item
          label="仓单品种"
          :label-width="formLabelWidth"
          prop="goodsCode"
        >
          <el-select
            v-model="contractForm.goodsCode"
            placeholder="请选择"
            @change="addGoodName"
          >
            <el-option
              v-for="item in goodsList"
              :key="item.code"
              :label="item.name"
              :value="item.code"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="仓单原价值"
          :label-width="formLabelWidth"
          prop="originalPrice"
        >
          <el-input v-model="contractForm.originalPrice"></el-input>
        </el-form-item>
        <el-form-item
          label="合同编号"
          :label-width="formLabelWidth"
          prop="contractNo"
        >
          <el-input
            v-model="contractForm.contractNo"
            @blur="isUnique(contractForm.contractNo)"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="联系人"
          :label-width="formLabelWidth"
          prop="customerLinkman"
        >
          <el-input v-model="contractForm.customerLinkman"></el-input>
        </el-form-item>
        <el-form-item
          label="联系电话"
          :label-width="formLabelWidth"
          prop="customerPhone"
        >
          <el-input v-model="contractForm.customerPhone"></el-input>
        </el-form-item>
        <el-form-item
          label="交货时间"
          :label-width="formLabelWidth"
          prop="deliveryTime"
        >
          <el-date-picker
            type="date"
            placeholder="选择交货时间"
            v-model="contractForm.deliveryTime"
            value-format="timestamp"
            style="width:200px"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="放款金额"
          :label-width="formLabelWidth"
          prop="totalLoan"
        >
          <el-input v-model="contractForm.totalLoan"></el-input>
        </el-form-item>
        <el-form-item
          label="合同交易到期日"
          :label-width="formLabelWidth"
          prop="tradeEndtime"
        >
          <el-date-picker
            type="date"
            placeholder="选择合同交易到期日"
            v-model="contractForm.tradeEndtime"
            value-format="timestamp"
            style="width:200px"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="交货地点"
          :label-width="formLabelWidth"
          prop="deliveryLocation"
        >
          <el-input v-model="contractForm.deliveryLocation"></el-input>
        </el-form-item>
        <el-form-item
          label="追保率"
          :label-width="formLabelWidth"
          prop="recoveryRate"
        >
          <el-input
            v-model="contractForm.recoveryRate"
            style="width:70px"
          ></el-input>%
        </el-form-item>
      </el-form>
      <span class="dialog-title">仓单信息</span>
      <div class="tableDate">
        <div
          class="button"
          style="width:3%;float:right;"
        >
          <P>
            <el-button
              class="el-icon-plus"
              @click.prevent="addRow()"
            ></el-button>
          </P>
          <p>
            <el-button
              class="el-icon-minus"
              @click.prevent="delData()"
            ></el-button>
          </p>
        </div>
        <div class="table">
          <el-table
            :data="wareHouseTable"
            ref="wareHouseTable"
            tooltip-effect="dark"
            border
            stripe
            style="width: 95%"
            @selection-change="selectRow"
          >
            <el-table-column
              type="selection"
              width="45"
              align="center"
            ></el-table-column>
            <el-table-column
              label="仓单编号"
              align="center"
              width="195"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.wwNo"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="商品名称"
              width="90"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="品牌/牌号"
              width="100"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.brand"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="质量标准"
              width="110"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.qualityStandard"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="包装规格"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.packingSpecification"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="数量"
              width="105"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.quantity"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="计量单位"
              width="80"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.unit"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库名称"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库地址"
              width="205"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系人"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkman"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系电话"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkPhone"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物位置"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物真实性"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAuthenticity"></el-input>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="addContract"
        >保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../../../validate.js";
import * as ajaxUrl from "../../../ajax.config.js";

export default {
  props: ["addContractVisible", "contractform"],
  data() {
    return {
      rules: rules,
      formLabelWidth: "135px",
      goodsList: [],
      wareHouseTable: [], //添加合同仓单信息
      addVisible: this.addContractVisible,
      contractForm: this.contractform,
    };
  },
  watch: {
    addContractVisible(val) {
      this.addVisible = val; //新增result的watch，监听变更并同步到myResult上
    },
    addVisible(val) {
      this.$emit("addContractVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    }
  },
  created() {
    this.getGoods();
  },
  methods: {
    addGoodName(selVal) {
      let obj = {};
      obj = this.goodsList.find(item => {
        //这里的userList就是上面遍历的数据源
        return item.code === selVal; //筛选出匹配数据
      });
      console.log(obj.name); //我这边的name就是对应label的
      this.contractForm.goodsName = obj.name;
    },
    getGoods() {
      this.axios
        .get(ajaxUrl.GETGOODS)
        .then(res => {
          if (res.data.resCode == 200) {
            this.goodsList = res.data.resData;
          } else {
            this.$message.error(res.data.resMsg);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 获取表格选中时的数据
    selectRow(val) {
      this.selectlistRow = val;
    },
    addContract() {
      this.$refs.contractForm.validate(valid => {
        if (valid) {
          let params = this.contractForm;
          params.warehouseWarrantList = this.wareHouseTable;
          this.axios
            .post(ajaxUrl.HOLDWARRANTADD, params)
            .then(res => {
              if (res.data.resCode == 200) {
                this.$message({
                  message: "添加成功",
                  type: "success"
                });
                this.addVisible = false;
                this.contractForm = {};
                this.wareHouseTable = [];
                this.$emit("searchInfo");
                this.$router.push("/GetContract");
              } else {
                this.$message.error(res.data.resMsg);
              }
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 增加行
    addRow() {
      let list = {
        rowNum: this.wareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.wareHouseTable.unshift(list);
    },
    // 删除选中行
    delData() {
      if (this.selectlistRow) {
        for (let i = 0; i < this.selectlistRow.length; i++) {
          let val = this.selectlistRow;
          // 获取选中行的索引的方法
          // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
          // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
          val.forEach((val, index) => {
            this.wareHouseTable.forEach((v, i) => {
              if (val.rowNum === v.rowNum) {
                // i 为选中的索引
                this.wareHouseTable.splice(i, 1);
              }
            });
          });
        }
      } else {
        console.log("No Array");
      }
      // 删除完数据之后清除勾选框
      this.$refs.wareHouseTable.clearSelection();
    },
    isUnique(contractNo) {
      let params = {
        contractNo: this.contractForm.contractNo
      };
      this.axios
        .post(ajaxUrl.UNIQUECONTRACTNO, params)
        .then(res => {
          if (res.data.resCode == 200) {
          } else {
            this.$message.error("该合同编号已存在");
            this.contractForm.contractNo = "";
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  components: {}
};
</script>

<style>
</style>



