<template>
  <div>
    <el-dialog
      title="出账记录"
      :visible.sync="accountRecordVisible_"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="支付回单">
          <div class="recordInfo">合同编号：{{recordList_.contractNo}}</div>
          <div class="recordInfo">交易客户：{{recordList_.customerName}}</div>
          <div>
            <el-carousel
              height="350px"
              :autoplay="false"
              ref="payCarousel"
            >
              <el-carousel-item
                v-for="item in recordList_.fileTypeZero"
                :key="item.index"
              >
                <img
                  :src="'http://192.168.2.221:8180' + item.filePath"
                  class="bannerImg"
                  style="width: 880px; height:350px; max-width: 100%; max-height: 100%; text-align: center;"
                />
              </el-carousel-item>
            </el-carousel>
            <div class="buttonGroup">
              <el-button
                type="primary"
                @click="download(0)"
              >下载</el-button>
              <el-button
                type="primary"
                @click="deletePayRecord(0)"
              >删除</el-button>
            </div>
          </div>
          <el-button
            size="small"
            type="primary"
            @click="sumbitUpload(0)"
            style="float:left;margin:5px 5px 0 0"
          >提交</el-button>
          <el-upload
            class="upload-demo"
            ref="upload"
            :action="returnAction()"
            :on-remove="handleRemove"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            multiple
            :on-exceed="handleExceed"
            :file-list="fileList"
            :auto-upload="false"
            :limit="1"
            :data="uploadData(0)"
            :before-upload="beforeUpload"
          >
            <el-button
              size="small"
              type="primary"
            >点击上传</el-button>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="增值税发票">
          <div class="recordInfo">合同编号：{{recordList_.contractNo}}</div>
          <div class="recordInfo">交易客户：{{recordList_.customerName}}</div>
          <div>
            <el-carousel
              height="350px"
              :autoplay="false"
              ref="payCarousel1"
            >
              <el-carousel-item
                v-for="item in recordList_.fileTypeOne"
                :key="item.index"
              >
                <img
                  :src="'http://192.168.2.221:8180' + item.filePath"
                  class="bannerImg"
                  style="width: 880px; height:350px; max-width: 100%; max-height: 100%; text-align: center;"
                />
              </el-carousel-item>
            </el-carousel>
            <div class="buttonGroup">
              <el-button
                type="primary"
                @click="download(1)"
              >下载
              </el-button>
              <el-button
                type="primary"
                @click="deletePayRecord(1)"
              >删除</el-button>
            </div>
          </div>
          <el-button
            size="small"
            type="primary"
            @click="sumbitUpload(1)"
            style="float:left;margin:5px 5px 0 0"
          >提交</el-button>
          <el-upload
            class="upload-demo"
            ref="upload1"
            :action="returnAction()"
            :on-remove="handleRemove"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            multiple
            :on-exceed="handleExceed"
            :file-list="fileList"
            :auto-upload="false"
            :limit="1"
            :data="uploadData(1)"
            :before-upload="beforeUpload"
          >
            <el-button
              size="small"
              type="primary"
            >点击上传</el-button>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../../../validate.js";
import * as ajaxUrl from "../../../ajax.config.js";

export default {
  props: ["accountRecordVisible", "recordList"],
  data() {
    return {
      accountRecordVisible_: this.accountRecordVisible,
      recordList_: this.recordList,
      fileList: []
    };
  },
  watch: {
    accountRecordVisible(val) {
      this.accountRecordVisible_ = val; //新增result的watch，监听变更并同步到myResult上
    },
    accountRecordVisible_(val) {
      this.$emit("accountRecordVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    },
    recordList(val) {
      this.recordList_ = val; //新增result的watch，监听变更并同步到myResult上
    }
  },
  methods: {
    returnAction() {
      return ajaxUrl.UPLOADIMAGE;
    },
    //上传功能
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeUpload(file) {
      const isJPG = (file.type.indexOf('image')==0)&&(/\.(?:jpg|png)$/.test(file.name))
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传失败！上传图片只能为 JPG 或 PNG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传失败！上传图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    //删除轮播图
    deletePayRecord(fileType) {
      if (fileType == 0) {
        this.$confirm("此操作删除该回单, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(
                ajaxUrl.DELETEIMAGE +
                  "/" +
                  this.recordList_.fileTypeZero[
                    this.$refs.payCarousel.activeIndex
                  ].id
              )
              .then(res => {
                if (res.data.resCode == 200) {
                  this.$message({
                    message: res.data.resMsg,
                    type: "success"
                  });
                  this.$emit("searchInfo");
                  this.accountRecordVisible_ = false;
                } else {
                  this.$message.error(res.data.resMsg);
                }
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else if (fileType == 1) {
        this.$confirm("此操作删除该发票, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(
                ajaxUrl.DELETEIMAGE +
                  "/" +
                  this.recordList_.fileTypeOne[
                    this.$refs.payCarousel1.activeIndex
                  ].id
              )
              .then(res => {
                if (res.data.resCode == 200) {
                  this.$message({
                    message: res.data.resMsg,
                    type: "success"
                  });
                  this.$emit("searchInfo");
                  this.accountRecordVisible_ = false;
                } else {
                  this.$message.error(res.data.resMsg);
                }
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    download(fileType) {
      let url =
        ajaxUrl.DOWNLOADZIP +
        "?contractId=" +
        this.recordList_.id +
        "&fileType=" +
        fileType;
      // window.location.href = url;
      window.open(url);
    },
    uploadData(type) {
      let params = {
        contractId: this.recordList_.id,
        fileType: type
      };
      return params;
    },
    sumbitUpload(type) {
      if (type == 0) {
        this.$refs.upload.submit();
      } else {
        this.$refs.upload1.submit();
      }
    },
    uploadSuccess(response, file, fileList) {
      this.$message({
        message: "上传成功",
        type: "success"
      });
      this.accountRecordVisible_ = false;
      this.fileList = [];
      this.$emit("searchInfo");
    },
    uploadError() {
      this.$message.error("上传失败");
    }
  },
  components: {}
};
</script>

<style>
</style>



