<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
      @openDialog="openDialog"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        label="操作"
        width="60"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'operate')"
            type="text"
            size="small"
          >
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
        width="150"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        :formatter="wwIdFormatter"
        width="180%"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'wareHouse')"
            type="text"
            size="small"
          >
            {{ scope.row.warehouseWarrantList.length > 1 ? scope.row.warehouseWarrantList[0].wwNo+"~"+ scope.row.warehouseWarrantList[scope.row.warehouseWarrantList.length-1].wwNo+"("+scope.row.warehouseWarrantList.length+")":((scope.row.warehouseWarrantList.length==1)?(scope.row.warehouseWarrantList[0].wwNo)+"(1)":"(0)")}}
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="status"
        label="仓单状态"
        sortable
        :formatter="statusSwitch"
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="交易客户"
        sortable
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="品种"
        sortable
        width="70"
      >
      </el-table-column>
      <el-table-column
        prop="contractFileList"
        label="出账记录"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'account')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="仓单总价值/总现价"
        :formatter="priceFormatter"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="deposit"
        label="保证金"
        sortable
        width="100"
      >
      </el-table-column>
      <el-table-column label="保值方案">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'method')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="totalLoan"
        label="对应放款金额"
        sortable
        width="120"
      >
        <template slot-scope="scope">
          {{tofixed(scope.row.totalLoan)}}
        </template>
      </el-table-column>
      <el-table-column
        label="当前担保率"
        sortable
        width="110"
      >
        <template slot-scope="scope">
          {{pledgeRateFormatter(scope.row)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="tradeEndtime"
        label="仓单交易到期日"
        sortable
        width="130"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.tradeEndtime)}}
        </template>
      </el-table-column>
      <!-- <el-table-column
        prop="loanEndtime"
        label="贷款到期日"
        sortable
        width="110"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.loanEndtime)}}
        </template>
      </el-table-column> -->
      <el-table-column
        prop="warehouseWarrantList"
        label="监控及影像信息"
        width="110"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'monitor')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <handleDialog
      :dialogFormVisible="dialogFormVisible"
      :form="form"
      :isDelete="true"
      @searchInfo="searchInfo"
      @dialogFormVisibleChange="dialogFormVisibleChange"
      @deleteRecord="deleteRecord"
    >
    </handleDialog>
    <addContract
      :addContractVisible="addContractVisible"
      :contractform="contractForm"
      @searchInfo="searchInfo"
      @addContractVisibleChange="addContractVisibleChange"
    ></addContract>
    <wareHouse
      :warehouseVisible="warehouseVisible"
      :wareHouseInfo="wareHouseInfo"
      :warehouseList="wareHouseList"
      @searchInfo="searchInfo"
      @wareHouseVisibleChange="wareHouseVisibleChange"
    ></wareHouse>
    <accountRecord
      :accountRecordVisible="accountRecordVisible"
      :recordList="recordList"
      @searchInfo="searchInfo"
      @accountRecordVisibleChange="accountRecordVisibleChange"
    ></accountRecord>
    <el-dialog
      title="保值方案"
      :visible.sync="methodVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加{{keepValuePlan.x>0 && isFinite(keepValuePlan.x)?tofixed(keepValuePlan.x):"-"}}吨的货物或{{keepValuePlan.y>0?tofixed(keepValuePlan.y):"-"}}元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
            @click="methodVisible = false"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <el-dialog
      title="监控及影像信息"
      :visible.sync="monitorVisible"
      :close="monitorClosed"
    >
      <el-autocomplete
        v-model="state"
        :fetch-suggestions="querySearchAsync"
        placeholder="请输入仓单编号"
        @select="handleSelect"
        style="margin-bottom:5px">
      </el-autocomplete>
      <el-tabs type="border-card">
        <el-tab-pane label="仓库">
          <table class="warehouseInfo">
            <tr>
              <th>仓库名称</th>
              <td>{{wareHouseInfo.warehouseName}}</td>
            </tr>
            <tr>
              <th>仓库地址</th>
              <td>{{wareHouseInfo.warehouseAddress}}</td>
            </tr>
            <tr>
              <th>联系人</th>
              <td>{{wareHouseInfo.linkman}}</td>
            </tr>
            <tr>
              <th>联系电话</th>
              <td>{{wareHouseInfo.linkPhone}}</td>
            </tr>
            <tr>
              <th>货物位置</th>
              <td>{{wareHouseInfo.goodsAddress}}</td>
            </tr>
            <tr>
              <th>货物真实性</th>
              <td>{{wareHouseInfo.goodsAuthenticity}}</td>
            </tr>
          </table>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from "../../components/Search.vue";
import handleDialog from "../../components/HandleDialog.vue";
import addContract from "./dialog/addContract.vue";
import wareHouse from "./dialog/warehouse.vue";
import accountRecord from "./dialog/accountRecord.vue";
import { rules } from "../../validate.js";
import * as ajaxUrl from "../../ajax.config.js";
import qs from "qs";

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam: {},

      tableData: [],
      contractForm: {}, //合同信息
      wareHouseInfo: {}, //仓单列表某条信息
      wareHouseList: [], //仓单列表
      recordList: {
        fileTypeZero: [],
        fileTypeOne: []
      },

      currentPage: 1,
      pageSize: 10,
      totalPage: 1,

      dialogFormVisible: false,
      addContractVisible: false,
      warehouseVisible: false,
      warehouseInnerVisible: false,
      accountRecordVisible: false,
      monitorVisible: false,
      methodVisible: false,

      form: {},
      pageType: 1,
      keepValuePlan: {},
      state: "",
      timeout: null,
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText;
      this.axios
        .get(ajaxUrl.HOLDWARRANTLIST, {
          params: {
            pageSize: this.pageSize,
            pageNum: this.currentPage,
            search: this.searchParam,
            orderField: orderField,
            orderType: orderType
          }
        })
        .then(res => {
          if(res.data.resCode == 200){
            this.tableData = res.data.resData;
            this.totalPage = res.data.page.total;
            this.pageSize = res.data.page.pageSize;
            this.currentPage = res.data.page.current;
            this.searchParam = {};
          }else{
            this.$message.error(res.data.resMsg)
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    //根据类型打开dialog
    openDialog(row, type) {
      if (type == "wareHouse") {
        //打开仓单面板
        this.warehouseVisible = true;
        if (row.warehouseWarrantList.length > 0) {
          this.wareHouseList = row.warehouseWarrantList;
          this.wareHouseInfo = row.warehouseWarrantList[0];
        }
          this.wareHouseList.customerName = row.customerName;
          this.wareHouseInfo.contractId = row.id;
      } else if (type == "operate") {
        //打开操作面板
        this.dialogFormVisible = true;
        this.addOperateStatus(row.id, 0);
        this.form = row;
        if (row.warehouseWarrantList.length > 1) {
          this.form.wareHouseId =
            row.warehouseWarrantList[0].wwNo +
            "~" +
            row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo;
        } else if ((row.warehouseWarrantList.length == 1)) {
          this.form.wareHouseId = row.warehouseWarrantList[0].wwNo;
        } else {
          this.form.wareHouseId = ("");
        }
      } else if (type == "contract") {
        //打开添加合同面板
        this.addContractVisible = true;
      } else if (type == "account") {
        //打开出账记录面板
        this.recordList = {
          fileTypeZero: [],
          fileTypeOne: []
        };
        this.accountRecordVisible = true;
        for (let i = 0; i < row.contractFileList.length; i++) {
          if (row.contractFileList[i].fileType == 0) {
            this.recordList.fileTypeZero.push(row.contractFileList[i]);
          }
          if (row.contractFileList[i].fileType == 1) {
            this.recordList.fileTypeOne.push(row.contractFileList[i]);
          }
        }
        this.recordList.contractNo = row.contractNo;
        this.recordList.customerName = row.customerName;
        this.recordList.id = row.id;
      } else if (type == "monitor") {
        this.monitorVisible = true;
        this.wareHouseInfo = {};
        this.state = "";
        this.wareHouseList = row.warehouseWarrantList;
      } else if (type == "method") {
        //打开保值方案面板
        this.methodVisible = true;
        this.keepValuePlan.x = row.originalPrice/row.goodsPrice - row.amount;
        this.keepValuePlan.y = row.originalPrice - row.amount*row.goodsPrice;
      }
    },
    dialogFormVisibleChange(val) {
      this.dialogFormVisible = val;
    },
    addContractVisibleChange(val) {
      this.addContractVisible = val;
    },
    wareHouseVisibleChange(val) {
      this.warehouseVisible = val;
    },
    accountRecordVisibleChange(val) {
      this.accountRecordVisible = val;
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    //删除合同或仓单
    deleteRecord(type) {
      if (type == "合同") {
        this.$confirm("此操作删除该合同, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(ajaxUrl.HOLDWARRANTDELETE+"/"+ this.form.id)
              .then(res => {
                if(res.data.resCode == 200){
                  this.$message({
                    type: "success",
                    message: "删除成功!"
                  });
                  this.dialogFormVisible = false;
                  this.$router.push("/GetContract");
                  this.searchInfo();
                }else{
                  this.$message.error(res.data.resMsg)
                }
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    //仓单状态处理
    statusSwitch(row, column) {
      switch (row.status) {
        case 0:
          return "存续";
          break;

        case 1:
          return "平仓";
          break;

        case 2:
          return "归档";
          break;
      }
    },
    //价格格式转换
    priceFormatter(row, column) {
      return this.tofixed(row.originalPrice) + " / " + this.tofixed(row.amount * row.goodsPrice);
    },
    //仓单显示格式转换
    wwIdFormatter(row, column) {
      if (row.warehouseWarrantList.length == 0) {
        return "————";
      } else if (row.warehouseWarrantList.length == 1) {
        return row.warehouseWarrantList[0].wwNo;
      } else if (row.warehouseWarrantList.length > 1) {
        return (
          row.warehouseWarrantList[0].wwNo +
          "~" +
          row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo +
          "(" +
          row.warehouseWarrantList.length +
          ")"
        );
      }
    },
    pledgeRateFormatter(row) {
      let num = new Number(row.totalLoan/(parseInt(row.goodsPrice) * parseInt(row.amount) + parseInt(row.deposit)));
      return ((num*100).toFixed(2)+"%")
    },
    //时间格式转换
    dateFormat(data) {
      return this.common.fullTime(data);
    },
    dateFormat2(row, column) {
      return this.common.fullTime(row.loanEndtime);
    },
    
    //远程搜索 下拉搜索功能
    querySearchAsync(queryString, cb) {
      let wareHouseList = this.wareHouseList;
      for (let i of wareHouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
      let results = queryString
        ? wareHouseList.filter(this.createStateFilter(queryString))
        : wareHouseList;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 300 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    handleSelect(item) {
      this.wareHouseInfo = item;
    },
    loadWarehouseId() {
      for (let i of this.wareHouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
    },
    monitorClosed(){
      this.wareHouseList = [];
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
    addOperateStatus(formId, formType){
      let params = {
        formId: formId,
        formType: formType
      };
      this.axios.post(ajaxUrl.INDEXINFO, params)
      .then(res => {
        if(res.data.resCode == 200){
          console.log("success")
        }else{
          this.$message.error(res.data.resMsg)
        }
      }).catch(err => {
        console.log(err);
      })
    },
    //
    tofixed(number) {
      return this.common.toFixed(number);
    },
    
  },
  components: {
    vSearch,
    handleDialog,
    addContract,
    wareHouse,
    accountRecord
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.dialog-title {
  font-size: 20px;
  margin-bottom: 20px;
  display: block;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px;
  margin-top: 25px;
}
.warehouseNum {
  float: left;
  height:30px;
  line-height: 30px;
}
.head-tool {
  position: absolute;
  top: 15px;
  left: 105px;
  
}
.delete-warehouse {
  margin-bottom: 15px;
  margin-left: 15px;
  width: 84px;
  float: left;
}
.delete-warehouse:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
.selectSearch {
  margin-left: 15px;
  margin-top: -5px;
}
.addWarehouse {
  margin-left: 15px;
}
.dialog-info {
  font-size: 16px;
  margin-bottom: 20px;
}
.dialog-name {
  color: blue;
}
.recordInfo {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-upload--text {
  width: 80px;
  height: 32px;
  border: 0px;
  margin-top: 5px;
}
.warehouseInfo {
  border-spacing: 0;
  border-collapse: collapse;
}
.warehouseInfo tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.warehouseInfo tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
</style>



