<template>
  <div>
    <el-dialog
      title="仓单编号："
      :visible.sync="wareHouseVisible"
      width="700px"
      @close="clearWareHouseInfo"
    >
      <div class="head-tool">
        <div class="warehouseNum">{{warehouseInfo.wwNo}}</div>
        <el-autocomplete
          class="selectSearch"
          v-model="state"
          :fetch-suggestions="querySearchAsync"
          placeholder="请输入内容"
          @select="handleSelect"
        ></el-autocomplete>
        <el-button
          class="addWarehouse"
          type="primary"
          @click="deleteRecord('仓单')"
        >删除该仓单</el-button>
        <el-button
          class="addWarehouse"
          type="primary"
          @click="warehouseInnerVisible = true"
        >追加仓单</el-button>
      </div>
      <div class="dialog-info">
        <span class="dialog-name">交易客户：</span>
        <span class="dialog-company">{{wareHouseList.customerName}}</span>
      </div>
      <el-form
        :model="warehouseInfo"
        style="margin-left:10px"
        :rules="rules"
      >
        <el-form-item
          label="商品种类"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.goodsName}}
        </el-form-item>
        <el-form-item
          label="商品名称"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.goodsName}}
        </el-form-item>
        <el-form-item
          label="品牌/牌号"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.brand}}
        </el-form-item>
        <el-form-item
          label="质量标准"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.qualityStandard}}
        </el-form-item>
        <el-form-item
          label="包装规格"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.packingSpecification}}
        </el-form-item>
        <el-form-item
          label="数量"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.quantity}}
        </el-form-item>
        <el-form-item
          label="计量单位"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.unit}}
        </el-form-item>
      </el-form>
      <el-dialog
        title="追加仓单："
        :visible.sync="warehouseInnerVisible"
        width="1200px"
        append-to-body
        @close="clearAddWareHouseTable"
      >
        <div class="tableDate">
          <div
            class="button"
            style="width:3%;float:right;"
          >
            <P>
              <el-button
                class="el-icon-plus"
                @click.prevent="addRow()"
              ></el-button>
            </P>
            <p>
              <el-button
                class="el-icon-minus"
                @click.prevent="delData()"
              ></el-button>
            </p>
          </div>
          <div class="table">
            <el-table
              :data="addWareHouseTable"
              ref="addWareHouseTable"
              tooltip-effect="dark"
              border
              stripe
              style="width: 95%"
              @selection-change="selectRow"
            >
              <el-table-column
                type="selection"
                width="45"
                align="center"
              ></el-table-column>
              <el-table-column
                label="仓单编号"
                align="center"
                width="195"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.wwNo"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="商品名称"
                width="90"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsName"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="品牌/牌号"
                width="100"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.brand"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="质量标准"
                width="110"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.qualityStandard"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="包装规格"
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.packingSpecification"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="数量"
                width="105"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.quantity"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="计量单位"
                width="80"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.unit"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="仓库名称"
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.warehouseName"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="仓库地址"
                width="205"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.warehouseAddress"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="联系人"
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.linkman"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="联系电话"
                width="165"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.linkPhone"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="货物位置"
                width="165"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsAddress"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="货物真实性"
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsAuthenticity"></el-input>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div
          slot="footer"
          class="dialog-footer"
        >
          <el-button
            type="primary"
            @click="submitWarehouse"
          >录 入</el-button>
        </div>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../../../validate.js";
import * as ajaxUrl from "../../../ajax.config.js";

export default {
  props: ["warehouseVisible", "wareHouseInfo", "warehouseList"],
  data() {
    return {
      state: "",
      rules: rules,
      formLabelWidth: "135px",
      wareHouseVisible: this.warehouseVisible,
      warehouseInnerVisible: false,
      addWareHouseTable: [],
      warehouseInfo: {},
      wareHouseList: [],
    };
  },
  watch: {
    warehouseVisible(val) {
      this.wareHouseVisible = val; //新增result的watch，监听变更并同步到myResult上
    },
    wareHouseVisible(val) {
      this.$emit("wareHouseVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    },
    wareHouseInfo(val) {
      this.warehouseInfo = val;
    },
    warehouseList(val) {
      this.wareHouseList = val; 
    },
  },
  methods: {
    //远程搜索 下拉搜索功能
    querySearchAsync(queryString, cb) {
      let wareHouseList = this.wareHouseList;
      for (let i of wareHouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
      let results = queryString
        ? wareHouseList.filter(this.createStateFilter(queryString))
        : wareHouseList;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 300 * Math.random());
    },
    handleSelect(item) {
      this.warehouseInfo = item;
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    clearAddWareHouseTable() {
      this.addWareHouseTable = [];
    },
    // 获取表格选中时的数据
    selectRow(val) {
      this.selectlistRow = val;
    },
    // 增加行
    addRow() {
      let list = {
        rowNum: this.addWareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.addWareHouseTable.unshift(list);
    },
    // 删除方法
    // 删除选中行
    delData() {
      if (this.selectlistRow) {
        for (let i = 0; i < this.selectlistRow.length; i++) {
          let val = this.selectlistRow;
          // 获取选中行的索引的方法
          // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
          // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
          val.forEach((val, index) => {
            this.addWareHouseTable.forEach((v, i) => {
              if (val.rowNum === v.rowNum) {
                // i 为选中的索引
                this.addWareHouseTable.splice(i, 1);
              }
            });
          });
        }
      }
      // 删除完数据之后清除勾选框
      this.$refs.addWareHouseTable.clearSelection();
    },
    submitWarehouse() {
      let params = {
        id: this.wareHouseInfo.contractId,
        warehouseWarrantList: this.addWareHouseTable
      };
      this.axios
        .post(ajaxUrl.BATCHWAREHOUSEWARRANT, params)
        .then(res => {
          if (res.data.resCode == 200) {
            this.warehouseInnerVisible = false;
            this.wareHouseVisible = false;
            this.$emit("searchInfo");
            this.addWareHouseTable = [];
          } else {
            this.$message.error(res.data.resMsg);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    clearWareHouseInfo() {
      this.warehouseInfo = {};
      this.state = "";
    },
    //删除合同或仓单
    deleteRecord(type) {
      if (type == "仓单") {
        this.$confirm("此操作删除该仓单, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios.delete(ajaxUrl.GETWAREHOUSEWARRANT+"/"+this.warehouseInfo.id)
            .then(res => {
              if(res.data.resCode == 200){
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.wareHouseVisible = false;
                this.$router.push("/GetContract");
                this.warehouseInfo = {},
                this.$emit("searchInfo");
              }else{
                this.$message.error(res.data.resMsg)
              }
            })
            .catch(err => {
              console.log(err);
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
  },
  components: {}
};
</script>

<style scoped>
.warehouseNum {
  width: 110px;
}
</style>



